.line {
  width: 100%;
  height: 1px;
  border-bottom: 1px dashed #ddd;
  margin: 40px 0;
}

/* ---------------------------------------------------
  SIDEBAR STYLE
----------------------------------------------------- */
.wrapper {
  display: flex;
  width: 100%;
  align-items: stretch;
}

#sidebar {
  min-width: 208px;
  /* max-width: 230px; */
  background: #17a2b8;
  color: #fff;
  transition: all 0.3s;
}

#sidebar.active {
  margin-left: -250px;
}



#sidebar .sidebar-header {
  padding: 20px;
  background: #17a2b8;
}

#sidebar ul.components {
  padding: 20px 0;
  /* border-bottom: 1px solid #47748b; */
}

#sidebar ul p {
  color: #fff;
  padding: 10px;
}

#sidebar ul li a {
  padding: 10px;
  font-size: 1.1em;
  display: block;
}

#sidebar ul li a:hover {
  color: #17a2b8;
  background: #fff;
}

#sidebar ul li.active>a,
a[aria-expanded="true"] {
  color: #fff;
  background: #007bff;
}

a[data-toggle="collapse"] {
  position: relative;
}

.dropdown-toggle::after {
  display: block;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

ul ul a {
  font-size: 0.9em !important;
  padding-left: 30px !important;
  background: #007bff;
}

ul.CTAs {
  padding: 20px;
}

ul.CTAs a {
  text-align: center;
  font-size: 0.9em !important;
  display: block;
  border-radius: 5px;
  margin-bottom: 5px;
}

a.download {
  background: #fff;
  color: #17a2b8;
}

a.article,
a.article:hover {
  background: #007bff !important;
  color: #fff !important;
}

.nav-item a{
  color: black;
}

.hideSidebar{
  display: none;
}
/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */

#content {
  width: 100%;
  /* padding: 0 0 20px 20px; */
  min-height: 100vh;
  transition: all 0.3s;
}
#logo-web {
  display: block;
  color: rgba(255,255,255,255);
}
#logo-mobile {
display: none;
color: rgba(255,255,255,255);
}

@media (max-width: 768px) {
  #logo-web {
      display: none;
      color: rgba(255,255,255,255);
  }
  #logo-mobile {
    display: block;
    color: rgba(255,255,255,255);
  }
}
.z_index{
  z-index: 1;
}
.form-group.required label:after {
  content:"*";
  color:red;
}
.react-datepicker__input-container input{
  /* padding:1px 0px !important; */
  width: 100%;
}